.container-sm{
    border: 1px solid red;
    height: 70vh;
    overflow: hidden;
}
.card{
    width: 100%;
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
}
.applythatGrayColor{
    color: #DCDCDC;
}
.card-header{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
}
.counter-wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    /* height: 25vh;
    width: 40vw; */
    margin: auto;
    border: 2px solid #7a1821;
    /* shape-outside: circle();
    clip-path: circle(); */
    animation: spinning 5s linear 2s infinite alternate;
    
}
.row-task-choice{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center
}
.col-sm-4 {
    display: flex;
    justify-content: center;
}

#list-of-tasks{
    width: 90%;
    background-color: black;
    color: #DCDCDC;
    border-radius: 5%;
}
@keyframes spinning {
    0%   {border-color:red}
    25%  {border-color:yellow}
    50%  {border-color:blue}
    75%  {border-color:green}
    100% {border-color:red}
}
.timer-area{
    display: flex;
    flex-direction: column;
}
.timer-components{
    display: flex;
    align-items: center;
    justify-content: center;
}
.div-btn-start{
    display: flex;
    margin-top: 5px;
    justify-content: center;
    align-items: center;
}
.btnController{
    margin-right: 5px;
    z-index: 99999;
}
.pointers{
    font-size:70px;
}